<template>
  <div>
    <Modal v-model="model1" width="360" draggable @on-cancel="cancel1">
      <div style="text-align:center;margin-top:30px">
        <p style="font-size:20px;margin-bottom:5px">是否删除该条记录？</p>
        <p style="color:#104E8B">删除后不可恢复</p>
      </div>
      <div slot="footer" style="text-align:center">
        <Button style="width:100px" type="error" @click="ok">删除</Button>
        <Button style="width:100px" @click="cancel">取消</Button>
      </div>
    </Modal>

    <Modal v-model="modelA" width="700" draggable @on-cancel="cancel1">
      <p slot="header">
        <span v-if="name==0" style="font-size:20px">新增音频资源</span>
        <span v-if="name==1" style="font-size:20px">新增视频资源</span>
        <span v-if="name==2" style="font-size:20px">新增电子书资源</span>
      </p>
      <div style="text-align:center">
        <Row>
          <Col span="24">
            <div style="float:left;margin-left:100px">
              <span style="color:#104E8B;margin-right:20px">标题</span>
              <Input v-model="newResource.title" placeholder="预设提示文字" style="width:200px"></Input>
            </div>
          </Col>
        </Row>
        <br />
        <Row>
          <Col span="10">
            <div style="float:left;margin-left:100px">
              <span style="color:#104E8B;margin-right:20px">封面</span>
              <Button
                icon="md-add"
                style="width:100px;height:100px;background:#eee;margin-right:10px"
                type="dashed"
                @click="addimg"
              >
                <br />
                <span style="margin-top:10px">上传</span>
              </Button>
              <input
                id="addMainImg"
                type="file"
                style="opacity: 0;height: 0;width: 0"
                accept=".png, .jpg, .jpeg"
              />
            </div>
          </Col>
          <Col span="14">
            <div
              style="width:100px;height:100px;border:1px solid black;border-style:dotted;"
              v-if="imgSrc"
            >
              <img style="width:100px;height:100px" :src="imgSrc" />
            </div>
          </Col>
        </Row>
        <Row>
          <Col span="24">
            <span style="color:#ccc;margin-right:70px">支持扩展名：.jpg .jpeg .png ...，建议图片尺寸</span>
          </Col>
        </Row>
        <br />
        <Row>
          <Col span="24">
            <div style="float:left;margin-left:100px">
              <span style="color:#104E8B;margin-right:20px">文件</span>
              <Button
                icon="ios-cloud-upload-outline"
                @click="addFile"
                style="margin-right:20px"
              >上传文件</Button>
              <input
                id="addMusic"
                type="file"
                style="opacity: 0;height: 0;width: 0"
                multiple
                accept=".mp3, .wav, .wma, .flac"
              />
              <input
                id="addvideo"
                type="file"
                style="opacity: 0;height: 0;width: 0"
                multiple
                accept=".mp4, .avi, .wmv, .mkv"
              />
              <input
                id="addBook"
                type="file"
                style="opacity: 0;height: 0;width: 0"
                multiple
                accept=".jpg, .jpeg, .png"
              />
            </div>
          </Col>
        </Row>
        <Row>
          <Col span="24">
            <span
              v-if="name==0"
              style="color:#ccc;margin-left:40px"
            >支持扩展名：.mp3 .wav .wma .flac ...，建议文件大小不超过100M</span>
            <span
              v-if="name==1"
              style="color:#ccc;margin-left:40px"
            >支持扩展名：.mp4 .avi .wmv .mkv ...，建议文件大小不超过100M</span>
            <span
              v-if="name==2"
              style="color:#ccc;margin-left:40px"
            >支持扩展名：..jpg .jpeg .png ...，建议图片尺寸</span>
          </Col>
        </Row>
        <Row v-if="fileList.length>0">
          <Col span="24">
            <div
              v-for="(item , index) in fileList"
              :key="index"
              style="display:flex;margin-left:150px"
            >
              <span>{{index+1}}.{{item.name}}</span>
            </div>
          </Col>
        </Row>
      </div>
      <div slot="footer" style="text-align:center">
        <Button style="width:100px" type="primary" @click="okAdd">确认</Button>
        <Button style="width:100px" @click="cancelAdd">取消</Button>
      </div>
    </Modal>

    <Modal v-model="modelE" width="700" draggable @on-cancel="cancel1">
      <p slot="header">
        <span v-if="name==0" style="font-size:20px">编辑音频资源</span>
        <span v-if="name==1" style="font-size:20px">编辑视频资源</span>
        <span v-if="name==2" style="font-size:20px">编辑电子书资源</span>
      </p>
      <div style="text-align:center">
        <Row>
          <Col span="24">
            <div style="float:left;margin-left:100px">
              <span style="color:#104E8B;margin-right:20px">标题</span>
              <Input v-model="fileEdit.title" style="width:200px" placeholder="待修改文字"></Input>
            </div>
          </Col>
        </Row>
        <br />
        <Row>
          <Col span="10">
            <div style="float:left;margin-left:100px">
              <span style="color:#104E8B;margin-right:20px">封面</span>
              <Button
                icon="md-add"
                style="width:100px;height:100px;background:#eee;"
                type="dashed"
                @click="editImg"
              >
                <br />
                <span style="margin-top:10px">上传</span>
              </Button>
              <input
                id="editImg"
                type="file"
                style="opacity: 0;height: 0;width: 0"
                accept=".png, .jpg, .jpeg"
              />
            </div>
          </Col>
          <Col span="14">
            <div style="width:100px;height:100px;border:1px solid black;border-style:dotted;">
              <img style="width:100px;height:100px" :src="imgSrc" />
            </div>
          </Col>
        </Row>
        <Row>
          <Col span="24">
            <span style="color:#ccc;margin-right:70px">支持扩展名：.jpg .jpeg .png ...，建议图片尺寸</span>
          </Col>
        </Row>
        <br />
        <Row>
          <Col span="24">
            <div style="float:left;margin-left:100px">
              <span style="color:#104E8B;margin-right:20px">文件</span>
              <Button
                icon="ios-cloud-upload-outline"
                style="margin-right:20px"
                @click="editFile"
              >上传文件</Button>
              <input
                id="editMusic"
                type="file"
                style="opacity: 0;height: 0;width: 0"
                accept=".mp3, .wav, .wma, .flac"
                multiple
              />
              <span></span>
            </div>
          </Col>
        </Row>
        <Row>
          <Col span="24">
            <span
              v-if="name==0"
              style="color:#ccc;margin-left:40px"
            >支持扩展名：.mp3 .wav .wma .flac ...，建议文件大小不超过100M</span>
            <span
              v-if="name==1"
              style="color:#ccc;margin-left:40px"
            >支持扩展名：.mp4 .avi .wmv .mkv ...，建议文件大小不超过100M</span>
            <span
              v-if="name==2"
              style="color:#ccc;margin-left:40px"
            >支持扩展名：..jpg .jpeg .png ...，建议图片尺寸</span>
          </Col>
        </Row>
        <Row v-if="fileEdit.fileList.length>0">
          <Col span="24">
            <div
              v-for="(item , index) in fileEdit.fileList"
              :key="index"
              style="display:flex;margin-left:150px"
            >
              <span>{{index+1}}.{{item.name}}</span>
            </div>
          </Col>
        </Row>
      </div>
      <div slot="footer" style="text-align:center">
        <Button style="width:100px" type="primary" @click="okEdit">确认</Button>
        <Button style="width:100px" @click="cancelEdit">取消</Button>
      </div>
    </Modal>

    <Tabs type="card" v-model="type" @on-click="change">
      <TabPane label="音频资源">
        <Row>
          <Col span="20">
            <span style="margin-right:10px">标题</span>
            <Input v-model="showAllProductParams.title" style="width:200px"></Input>
          </Col>
          <Col span="4">
            <Button type="primary" style="margin-right:20px" @click="getSearch">搜索</Button>
            <Button type="info" @click="Add">新增</Button>
          </Col>
        </Row>
        <br />
        <Table border :columns="columns1" :data="data1">
          <template slot-scope="{ row, index }" slot="product">
            <div style="text-align:center">
              <img :src="row.cover" style="width:40px;height:40px;margin-top:10px" />
            </div>
          </template>
          <template slot-scope="{ row, index }" slot="edit">
            <a
              size="small"
              type="primary"
              @click="toEdit(row.id)"
              style="margin-top:2px;width:60px;color:blue;padding-right:10px"
            >编辑</a>
            <a
              size="small"
              type="primary"
              @click="Delete(row.id)"
              style="margin-top:2px;width:60px;color:blue"
            >删除</a>
          </template>
        </Table>
        <br />
        <Row>
          <Col span="24" style="text-align:right;">
            <Page
              :total="total1"
              :current="showAllProductParams.page"
              :page-size="showAllProductParams.pageSize"
              show-total
              show-sizer
              show-elevator
              @on-change="changePage"
              @on-page-size-change="changePageSize"
            ></Page>
          </Col>
        </Row>
      </TabPane>

      <TabPane label="视频资源">
        <Row>
          <Col span="20">
            <span style="margin-right:10px">标题</span>
            <Input v-model="showAllProductParams.title" style="width:200px"></Input>
          </Col>
          <Col span="4">
            <Button type="primary" style="margin-right:20px" @click="getSearch">搜索</Button>
            <Button type="info" @click="Add">新增</Button>
          </Col>
        </Row>
        <br />
        <Table border :columns="columns2" :data="data2">
          <template slot-scope="{ row, index }" slot="product">
            <div style="text-align:center">
              <img :src="row.cover" style="width:40px;height:40px;margin-top:10px" />
            </div>
          </template>
          <template slot-scope="{ row, index }" slot="edit">
            <a
              size="small"
              type="primary"
              @click="toEdit(row.id)"
              style="margin-top:2px;width:60px;color:blue;padding-right:10px"
            >编辑</a>

            <a
              size="small"
              type="primary"
              @click="Delete(row.id)"
              style="margin-top:2px;width:60px;color:blue"
            >删除</a>
          </template>
        </Table>
        <br />
        <Row>
          <Col span="24" style="text-align:right;">
            <Page
              :total="total2"
              :current="showAllProductParams.page"
              :page-size="showAllProductParams.pageSize"
              show-total
              show-sizer
              show-elevator
              @on-change="changePage"
              @on-page-size-change="changePageSize"
            ></Page>
          </Col>
        </Row>
      </TabPane>

      <TabPane label="电子书资源">
        <Row>
          <Col span="20">
            <span style="margin-right:10px">标题</span>
            <Input v-model="showAllProductParams.title" style="width:200px"></Input>
          </Col>
          <Col span="4">
            <Button type="primary" style="margin-right:20px" @click="getSearch">搜索</Button>
            <Button type="info" @click="Add">新增</Button>
          </Col>
        </Row>
        <br />
        <Table border :columns="columns3" :data="data3">
          <template slot-scope="{ row, index }" slot="product">
            <div style="text-align:center">
              <img :src="row.cover" style="width:40px;height:40px;margin-top:10px" />
            </div>
          </template>
          <template slot-scope="{ row, index }" slot="edit">
            <a
              size="small"
              type="primary"
              @click="toEdit(row.id)"
              style="margin-top:2px;width:60px;color:blue;padding-right:10px"
            >编辑</a>
            <a
              size="small"
              type="primary"
              @click="Delete(row.id)"
              style="margin-top:2px;width:60px;color:blue"
            >删除</a>
          </template>
        </Table>
        <br />
        <Row>
          <Col span="24" style="text-align:right;">
            <Page
              :total="total3"
              :current="showAllProductParams.page"
              :page-size="showAllProductParams.pageSize"
              show-total
              show-sizer
              show-elevator
              @on-change="changePage"
              @on-page-size-change="changePageSize"
            ></Page>
          </Col>
        </Row>
      </TabPane>
    </Tabs>
  </div>
</template>
<script>
export default {
  name: "mediaResource",
  data() {
    return {
      type: 0,
      model1: false,
      modelA: false,
      modelE: false,
      imgSrc: "",
      fileList: [],
      name: 0,
      fileEdit: {
        id: "",
        title: "",
        cover: "",
        type: 0,
        fileList: []
      },
      newResource: {
        title: "",
        cover: "",
        type: 0,
        fileList: []
      },
      showAllProductParams: {
        type: 0,
        title: "",
        page: 1,
        pageSize: 10
      },
      columns1: [
        {
          title: "序号",
          align: "center",
          type: "index"
        },
        {
          title: "标题",
          key: "title",
          align: "center"
        },
        {
          title: "封面缩略图",
          align: "center",
          slot: "product"
        },
        {
          title: "创建时间",
          key: "createTime",
          align: "center"
        },
        {
          title: "操作",
          align: "center",
          slot: "edit"
        }
      ],
      data1: [],
      total1: 0,
      columns2: [
        {
          title: "序号",
          type: "index",
          align: "center"
        },
        {
          title: "标题",
          key: "title",
          align: "center"
        },
        {
          title: "封面缩略图",
          align: "center",
          slot: "product"
        },
        {
          title: "创建时间",
          key: "createTime",
          align: "center"
        },
        {
          title: "操作",
          align: "center",
          slot: "edit"
        }
      ],
      data2: [],
      total2: 0,
      columns3: [
        {
          title: "序号",
          type:'index',
          align: "center"
        },
        {
          title: "标题",
          key: "title",
          align: "center"
        },
        {
          title: "封面缩略图",
          align: "center",
          slot: "product"
        },
        {
          title: "创建时间",
          key: "createTime",
          align: "center"
        },
        {
          title: "操作",
          align: "center",
          slot: "edit"
        }
      ],
      data3: [],
      total3: 0
    };
  },
  methods: {
    getData() {
      let name = this.name;
      this.newResource.type = this.name + 1;
      this.fileEdit.type = this.name + 1;
      this.showAllProductParams.type = this.name + 1;
      if (name == 0) {
        this.$axios
          .post("/api/resource/getResourceList", {
            type: 1,
            page: 1,
            pageSize: 10
          })
          .then(res => {
            console.log(res);
            this.data1 = res.data.data.records;
            this.total1 = res.data.data.total;
          });
      }
      if (name == 1) {
        this.$axios
          .post("/api/resource/getResourceList", {
            type: 2,
            page: 1,
            pageSize: 10
          })
          .then(res => {
            console.log(res);
            this.data2 = res.data.data.records;
            this.total2 = res.data.data.total;
          });
      }
      if (name == 2) {
        this.$axios
          .post("/api/resource/getResourceList", {
            type: 3,
            page: 1,
            pageSize: 10
          })
          .then(res => {
            console.log(res);
            this.data3 = res.data.data.records;
            this.total3 = res.data.data.total;
          });
      }
    },
    getSearch() {
      if (this.showAllProductParams.type == 1) {
        this.$axios
          .post("api/resource/getResourceList", this.showAllProductParams)
          .then(res => {
            console.log(res);
            this.data1 = res.data.data.records;
            this.total1 = res.data.data.total;
          });
      }
      if (this.showAllProductParams.type == 2) {
        this.$axios
          .post("api/resource/getResourceList", this.showAllProductParams)
          .then(res => {
            console.log(res);
            this.data2 = res.data.data.records;
            this.total2 = res.data.data.total;
          });
      }
      if (this.showAllProductParams.type == 3) {
        this.$axios
          .post("api/resource/getResourceList", this.showAllProductParams)
          .then(res => {
            console.log(res);
            this.data3 = res.data.data.records;
            this.total3 = res.data.data.total;
          });
      }
    },
    changePage(page) {
      this.showAllProductParams.page = page;
      this.getSearch();
    },
    changePageSize(pageSize) {
      this.showAllProductParams.pageSize = pageSize;
      this.getSearch();
    },
    Delete() {
      this.model1 = true;
    },
    Add() {
      this.modelA = true;
    },
    toEdit(id) {
      this.modelE = true;
      console.log(id);
      this.$axios.post("api/resource/getResource", { id: id }).then(res => {
        console.log(res);
        this.fileEdit.id = id;
        this.fileEdit.title = res.data.data.title;
        this.fileEdit.cover = res.data.data.cover;
        for (let i = 0; i < res.data.data.resourceFileList.length; i++) {
          this.fileEdit.fileList.push({
            name: res.data.data.resourceFileList[i].fileName,
            path: res.data.data.resourceFileList[i].filePath
          });
        }
        this.imgSrc = res.data.data.cover;
      });
    },
    addimg() {
      const self = this;
      let addMainImg = document.getElementById("addMainImg");
      addMainImg.click();
      addMainImg.onchange = function(e) {
        let form = new FormData();
        form.append("img", this.files[0]);
        self.$axios.post("api/file/upload", form).then(res => {
          console.log(res.data);
          self.newResource.cover = res.data.data.filePath;
        });
        const oFReader = new FileReader();
        oFReader.readAsDataURL(this.files[0]);
        oFReader.onload = function(oFREvent) {
          console.log(oFREvent);
          self.imgSrc = oFREvent.target.result; // base64
        };
      };
    },
    addFile() {
      const self = this;
      let addFile = "";
      if (self.name == 0) {
        addFile = document.getElementById("addMusic");
      }
      if (self.name == 1) {
        addFile = document.getElementById("addVideo");
      }
      if (self.name == 2) {
        addFile = document.getElementById("addBook");
      }
      console.log('name',self.name)
      console.log('addFile',addFile)
      addFile.click();
      addFile.onchange = function(e) {
        console.log(this.files);
        for (let i = 0; i < this.files.length; i++) {
          let form = new FormData();
          form.append("img", this.files[i]);
          self.$axios.post("api/file/upload", form).then(res => {
            console.log(res.data);
            self.newResource.fileList.push({
              path: res.data.data.filePath,
              name: res.data.data.name
            });
            self.fileList.push({ name: res.data.data.name });
          });
        }
      };
    },
    editImg() {
      const self = this;
      let editImg = document.getElementById("editImg");
      editImg.click();
      editImg.onchange = function(e) {
        let form = new FormData();
        form.append("img", this.files[0]);
        self.$axios.post("api/file/upload", form).then(res => {
          console.log(res.data);
          self.fileEdit.cover = res.data.data.filePath;
        });
        const oFReader = new FileReader();
        oFReader.readAsDataURL(this.files[0]);
        oFReader.onload = function(oFREvent) {
          self.imgSrc = oFREvent.target.result; // base64
        };
      };
    },
    editFile() {
      const self = this;
      let editFile = "";
      if (self.name == 0) {
        editFile = document.getElementById("addMusic");
      }
      if (self.name == 1) {
        editFile = document.getElementById("addVideo");
      }
      if (self.name == 2) {
        editFile = document.getElementById("addBook");
      }
      editFile.click();
      editFile.onchange = function(e) {
        let list = [];
        for (let i = 0; i < this.files.length; i++) {
          let form = new FormData();
          form.append("img", this.files[i]);
          self.$axios.post("api/file/upload", form).then(res => {
            console.log(res.data);
            list.push({
              path: res.data.data.filePath,
              name: res.data.data.name
            });
          });
        }
        self.fileEdit.fileList = list;
      };
    },
    okAdd() {
      console.log(this.newResource);
      if (
        this.newResource.title &&
        this.newResource.cover &&
        this.newResource.fileList
      ) {
        this.$axios
          .post("api/resource/addResource", this.newResource)
          .then(res => {
            console.log(res);
            this.modelA = false;
            this.$Message.success("添加成功!");
            this.getData();
          });
      } else {
        this.$Message.error("添加失败!");
      }
    },
    cancelAdd() {
      this.modelA = false;
      this.imgSrc = "";
      this.$Message.info("用户点击了取消!");
    },
    okEdit() {
      console.log(this.fileEdit);
      if (
        this.fileEdit.title &&
        this.fileEdit.cover &&
        this.fileEdit.fileList
      ) {
        this.$axios
          .post("api/resource/editResource", this.fileEdit)
          .then(res => {
            console.log(res);
            this.modelE = false;
            this.$Message.success("修改成功!");
            this.getData();
          });
      } else {
        this.$Message.error("修改失败!");
      }
    },
    cancelEdit() {
      this.$Message.info("用户点击了取消");
      this.modelE = false;
    },
    Delete(id) {
      this.model1 = true;
      this.deleteId = id;
    },
    ok() {
      console.log(this.deleteId);
      let id = this.deleteId;
      this.$axios.post("api/resource/deleteResource", { id: id }).then(res => {
        console.log(res);
        this.model1 = false;
        this.$Message.success("删除成功!");
        this.getData();
      });
    },
    cancel() {
      this.model1 = false;
      this.$Message.info("用户点击了取消！");
    },
    cancel1() {
      this.imgSrc = "";
    },
    change(name) {
      this.name = name;
      this.newResource.type = name + 1;
      this.fileEdit.type = name + 1;
      this.showAllProductParams.type = name + 1;
      if (name == 0) {
        this.$axios
          .post("/api/resource/getResourceList", {
            type: 1,
            page: 1,
            pageSize: 10
          })
          .then(res => {
            console.log(res);
            this.data1 = res.data.data.records;
            this.total1 = res.data.data.total;
          });
      }
      if (name == 1) {
        this.$axios
          .post("/api/resource/getResourceList", {
            type: 2,
            page: 1,
            pageSize: 10
          })
          .then(res => {
            console.log(res);
            this.data2 = res.data.data.records;
            this.total2 = res.data.data.total;
          });
      }
      if (name == 2) {
        this.$axios
          .post("/api/resource/getResourceList", {
            type: 3,
            page: 1,
            pageSize: 10
          })
          .then(res => {
            console.log(res);
            this.data3 = res.data.data.records;
            this.total3 = res.data.data.total;
          });
      }
    }
  },
  mounted() {
    this.getData();
  }
};
</script>
